a {
  text-decoration: none;
  color: #E0ECFF;
}
@-moz-keyframes tabLite {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(24px);
  }
}
@-webkit-keyframes tabLite {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(24px);
  }
}
@keyframes tabLite {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(24px);
  }
}
@-moz-keyframes tabPro {
  0% {
    transform: translateX(24px);
  }
  100% {
    transform: translateX(0px);
  }
}
@-webkit-keyframes tabPro {
  0% {
    transform: translateX(24px);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes tabPro {
  0% {
    transform: translateX(24px);
  }
  100% {
    transform: translateX(0px);
  }
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 64px;
  background: #0A172B;
}
.header .menu {
  display: flex;
  transform: inherit;
}
.header .left {
  display: flex;
  align-items: center;
}
.header .left sup {
  font-size: 12px;
}
.header .left .logo {
  margin-left: 48px;
  display: flex;
  align-items: center;
}
.header .left .logo > a {
  color: #E0ECFF;
  text-decoration: none;
  display: flex;
}
.header .left .logo .name {
  margin: auto 12px;
  color: #E0ECFF;
  font-size: 20px;
  white-space: nowrap;
}
.header .right {
  display: flex;
  align-items: center;
  margin-right: 24px;
}
.header .right .deri-lite-pro-tab {
  width: 80px;
  height: 24px;
  border-radius: 1000px;
  margin-right: 64px;
  cursor: pointer;
}
.header .right .deri-lite-pro-tab .pro-lite-check {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 24px;
  border-radius: 1000px;
  color: #E0ECFF;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  background: radial-gradient(303.03% 118.88% at 32.14% 100%, #5577FD 0%, #99ADFF 100%);
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.15);
  position: relative;
  top: -1px;
  left: -1px;
}
.header .right .deri-lite-pro-tab .pro-type {
  animation: tabPro 0.2s 0s linear;
  transform: translateX(0px);
}
.header .right .deri-lite-pro-tab .lite-type {
  animation: tabLite 0.2s 0s linear;
  transform: translateX(24px);
  color: #5577FD;
  background: radial-gradient(356.96% 137.44% at 29.46% 100%, #CEE0FF 0%, #FFF 100%);
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.14);
}
.header .right .isPro {
  background: linear-gradient(270deg, #203B60 0%, rgba(32, 59, 96, 0) 100%);
  border: 1.5px solid #6886FE;
}
.header .right .isLite {
  background: linear-gradient(268deg, rgba(224, 236, 255, 0) 0%, #E0ECFF 100%);
  border: 1px solid #FFF;
}
.header .right .work-href {
  margin-right: 16px;
}
.header .small-menu {
  margin-left: 24px;
  cursor: pointer;
}
@media only screen and (min-width: 0px) {
  .header .right {
    margin-right: 0px;
  }
}
@media only screen and (min-width: 1140px) {
  .header .right {
    margin-right: 24px;
  }
}
@-moz-keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
  }
}
@-webkit-keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes menuFadeout {
  0% {
    transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
  }
}
@media only screen and (max-width: 375px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .m-connect {
    width: 130px !important;
  }
  .header .menu {
    display: flex;
    transform: translateX(-100%);
    -moz-animation: menuFadeout 0.2s 0s linear;
    -webkit-animation: menuFadeout 0.2s 0s linear;
    animation: menuFadeout 0.2s 0s linear;
  }
  .header .small-menu {
    display: block;
  }
}
@media only screen and (max-width: 760px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .m-connect {
    width: 130px !important;
  }
  .header .menu {
    display: flex;
    transform: translateX(-100%);
    -moz-animation: menuFadeout 0.2s 0s linear;
    -webkit-animation: menuFadeout 0.2s 0s linear;
    animation: menuFadeout 0.2s 0s linear;
  }
  .header .small-menu {
    display: block;
  }
  .header .network-select #network-select {
    left: calc(100% - 180px) !important;
  }
  .header .network-select .name,
  .header .network-select .network-select-icon {
    display: none;
  }
  .header .wallet-view .acc-wrapper img {
    margin-right: 0;
  }
  .header .wallet-view .acc-wrapper .address {
    display: none;
  }
}
@media only screen and (max-width: 1140px) {
  .header .left .logo {
    margin-left: 32px;
  }
  .header .right .deri-lite-pro-tab {
    display: none;
  }
  .header .feed-back {
    display: none;
  }
  .header .menu {
    display: flex;
    transform: translateX(-100%);
    -moz-animation: menuFadeout 0.2s 0s linear;
    -webkit-animation: menuFadeout 0.2s 0s linear;
    animation: menuFadeout 0.2s 0s linear;
  }
  .header .small-menu {
    display: block;
  }
  .header .wallet-view {
    margin-right: 8px;
  }
  .header .lang-select {
    margin-right: 8px;
  }
}
@media only screen and (min-width: 1141px) {
  .header .menu {
    display: flex;
    transform: inherit;
  }
  .header .small-menu {
    display: none;
  }
  .header .lang-select,
  .header .network-select {
    display: flex;
  }
}
