.wallet-modal {
  &-body {
    display: flex;
    width: 560px;
    flex-wrap: wrap;
    justify-content: center;

    &-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 32px 32px 32px 16px;
      margin: 0 16px 16px 0;
      border: 1px solid #203B60;
      // border-bottom: 1px solid transparent;
      width: 248px;
      height: 88px;

      .name {
        display: flex;
        align-items: center;

        .spinner-border {
          margin-left: 8px;
        }
      }

      .status {
        display: flex;
        align-items: center;
        width: 16px;

        .connected {
          border: 2px solid #CD7A37;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: #CD7A37;
        }
      }
    }

    &-particle {
      width: 100%;
      padding: 24px 16px;

      .desc {
        display: flex;

        .line {
          width: 33%;
          border-bottom: 1px solid #203B60;
          height: 10px;
        }

        .label {
          font-size: 12px;
          color: #93A1B8;
          text-align: center;
          padding: 0 8px;
        }
      }

      .auth-type {
        display: flex;
        justify-content: space-between;
        padding: 20px 34px 0;

        img {
          cursor: pointer;
        }
      }
    }

    &-item:hover {
      cursor: pointer;
      border: 1px solid #CD7A37 !important;
      background: url(../../assets/img/wallet-hover-bg.svg) no-repeat;

    }
  }

  &-footer {
    a {
      color: #93A1B8;
    }
  }
}

@media only screen and (min-width : 375px) and (max-width :760px) {
  .wallet-modal {
    &-body {
      display: flex;
      width: 375px;
      justify-content: center;

      &-item {
        margin: 0 16px;
        border: 1px solid #203B60;
        width: 100%;
        border-bottom: 0px;
        background-size: 100% !important;
      }

      &-item:nth-last-child(2) {
        border-bottom: 1px solid #203B60;
      }

      &-particle {
        .desc .line {
          width: 24%;
        }
      }
    }
  }
}