#__react-alert__ {
  div>div {
    pointer-events: auto !important;
  }

  div {
    z-index: 999999 !important;
  }
}

.network-state {
  width: 336px;
  background: #1A2D4A;
  border-radius: 4px;
  font-size: 16px;
  color: #E0ECFF;
  position: fixed;
  top: 68px;
  right: 16px;
  z-index: 9999999;

  .message-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 16px;
    height: 56px;
    border-bottom: 1px solid #CE5E5E;
    position: relative;

    .close-icon {
      width: 20px;
      height: 20px;
      border: 1px solid #E0ECFF;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .message-title-text {
      display: flex;
      align-items: center;

      img {
        margin-right: 4px;
      }
    }
  }

  .trade-info-box {
    width: 100%;
    height: 84px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 16px;
    font-size: 14px;
    color: #93A1B8;
  }


}